.contactPage{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 80px;
    padding:20px 20px 5%;
}
.contactPage__numbers li{
    color: #fff;
    text-shadow: 1px 1px 1px #000;
    font-size: 20px;
}
.contactPage__numbers li span{
    color: #ffffff9c;
    text-shadow: none;
}
.contactPage__numbers li:not(:first-of-type){
    padding-top: 20px;
}
iframe{
    width: 400px;
    height: 400px;
}
br{
    display: none;
}



@media screen and (max-width:900px){
    .contactPage{
        flex-direction: column-reverse;
        gap:40px;
    }
}
@media screen and (max-width:500px){
    iframe{
        width: 350px;
        height: 350px;
    }
}
@media screen and (max-width:450px){
    iframe{
        width: 300px;
        height: 300px;
    }
    .contactPage__numbers{
        padding: 0 20px;
    }
    br{
        display: block;
    }
}
@media screen and (max-width:380px){
    iframe{
        width: 250px;
        height: 250px;
    }
}